html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

  body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin: 0;
  }

  .contenedor{
    margin: 0 auto;
    max-width: 1440px;
    padding: 10px 30px;
  }

  a{
    text-decoration: none;
    color: #292929;
  }